header{
    background-image: url('./animalcrossing.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    width: 100%;
    height: 128px;
}

h2{
    text-align: center;
}

ul{
    margin: auto;
    background-color: #f3eed8;
    padding: 0.2rem 0.7rem;
    border-radius: 7px;
}

.creature-data{
    list-style: none;
    padding: 0.5rem;
    margin: 0.5rem 0;
    background-color: #f1e7cc;
    border-radius: 7px;
    box-shadow: 2px 3px 0 #ebdfbf;
    color: #3f3f3f;
    border: 5px solid transparent;
}

.creature-data:hover{
    border: 5px solid #f8a9af;
}

h3{
    display: inline-block;
    padding: 0;
    margin: 0;
    grid-area: 'name';
}

.headline-data{
    display: grid;
    grid-template-columns: 50%;
    grid-template-areas:
        'name cost';
    align-items: center;
}

.location{
    grid-area: 'place';
    text-align: center;
}

.cost{
    grid-area: 'cost';
    text-align: right;
}

.add-to-pocket{
    grid-area: 'pocketBtn';
    width: 30px;
    height: 30px;
    padding: 0;
    border-radius: 7px;
    background-color: #efe05f;
    border: none;
    box-shadow: 2px 1px 0 #d6c68b;
    color: #715b43;
    font-weight: 900;
    font-size: 1.5rem;
    justify-self: end;
}


.main-search {
    display: flex;
    align-content: center;
    justify-content: space-between;
}

.searchForm{
    transition: margin .1s;
    transition-timing-function: ease-out;
}

.filter-open{
    margin-bottom: 150px;
}

.searchForm input{
    font-size: 1.5rem;
    padding: 0.45rem;
    border-radius: 5px;
    border: solid 3px #f1e7cc;
    background-color: #f3eed8;
    color: #3f3f3f;
    margin-bottom: .5rem;
    width: 60%;
    height: 50px;
}

.searchForm button{
    font-size: 1.2rem;
    border-radius: 5px;
    border: none;
    background-color: #f3eed8;
    box-shadow: 2px 3px 0 #ebdfbf;
    height: 50px;
    margin: 0;
    justify-self: flex-end;
}


.filter-buttons{
    transition: all 0.1s;
    transition-timing-function: ease-in-out;
    visibility: visible;
    width: 100%;
}

.hide-me{
    opacity: 0;
    visibility: hidden;
}


.filter-buttons button{
    width: 100%;
    margin: 0;
    margin-bottom: .5rem;
}

body{
    background-color: #97d8d8;
    font-family: sans-serif;
}

*{
    box-sizing: border-box;
}

.my-pocket{
    background-color: #f1f1f1;
    border-radius: 7px;
    margin-bottom: 5px;
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
}

.pocket-slot{
    border-radius: 50%;
    background-color: #e0e0e0;
    height: 20px;
    width: 20px;
    margin: .5rem;
}